import React, { Component } from 'react';
import { Row, Col } from 'react-bootstrap';

export default class Subtotal extends Component{
  render(){
    return(
      <Row className="show-grid">
        <Col md={7}>Subtotal</Col>
        <Col md={5}>{`₹${this.props.price}`}</Col>
      </Row>
    )
  }
}
