import React, { Component } from 'react';
import { Button, Collapse, Media, Row, Col } from 'react-bootstrap';

export default class ItemDetails extends Component{
  constructor(props){
    super(props);
    this.state = {
      open:false
    };
  }

render(){
  return(
    <div>
      <Button
        className="item-details-button"
        variant="link"
        onClick={()=>this.setState({open:!this.state.open})}
      >
      {this.state.open === false ? `See`: `Hide`} item details
      {this.state.open === false ? ` +`: ` -`}
      </Button>
      <Collapse in={this.state.open}>
        <div>
            <Media>
              <Media.Body><Row className="show-grid">
                <Col md={2}></Col>
                <Col md={6}>
                  <img width={66} height={100} alt="thumbnail"  src="https://images-na.ssl-images-amazon.com/images/I/71gLBOMtCTL._SL1500_.jpg" />
                </Col>
                <Col md={4}></Col>
              </Row>

              </Media.Body>
              <Media.Body>
                <p>Samsung Galaxy M20</p>
                <Row className="show-grid">
                  <Col md={6}>
                    <strong>{`₹${this.props.price}`}</strong>
                    <br />
                    <strong className="price-strike"> {`₹12987.35`}</strong>
                  </Col>
                  <Col md={6}> Qty. 1</Col>
                </Row>
              </Media.Body>
            </Media>
        </div>
      </Collapse>
    </div>
  )
}
}
