import React, { Component } from 'react';
import { Row, Col, Tooltip, OverlayTrigger } from 'react-bootstrap';

var styles = {
    PickupSavings:{
      textDecoration: 'underline'
    },
    totalSavings:{
      color:'red',
      fontWeight:800
    }
}

export default class PickupSavings extends Component{
  render(){
    const tooltip = (
      <Tooltip id="tooltip">
        <p>Pick up your order and get 75/- as flat discount</p>
      </Tooltip>

    );
    return(
      <Row className="show-grid">
        <Col md={7}>
          <OverlayTrigger placement="botton" overlay={tooltip}>
          <div style={styles.PickupSavings}>Pickup Savings</div>
          </OverlayTrigger>
        </Col>
        <Col style={styles.totalSavings} md={5}> {`₹${this.props.price}`} </Col>
      </Row>
    )
  }
}
